.related{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.5rem;
    margin: 0.9375rem;

    &__card{
        display: flex;
        flex-direction: column;
        flex: 1 0 10.7rem;
        max-width: 20.375rem;
        overflow: hidden;
        background: #fff;
        color: #3e3e95;
        border-radius: 0.3125rem;
        box-shadow: 0.1825rem 0.125rem 0.875rem rgba(#262631, 0.2);
        font-size: 1rem;
        transition: all 150ms ease-in-out;
    }

    &__image{
        flex: 0 0 7.5rem;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        border-radius: 0.3125rem;
    }

    &__title{
        font-size: 1.125rem;
        font-weight: 700;
        padding: 0.5rem;

        &:hover{
            transform: translateY(-0.05rem);
            color: rgb(81, 94, 165);
        }
    }
}